export default {
    data() {
        return {
            userData: null
        };
    },
    onLoad() {
        this.getdata()

    },
    methods: {

        getdata() {
            this.$u.get('/get_user').then(res => {
                if (res.errorCode !== 0) return;
                this.userData = res.data[0]
            })
        },
        to(page) {
            uni.navigateTo({
                        url: `../${page}/${page}`
                    })
     
        },
        login() {
            uni.navigateTo({
                url: "../login/login"
            })
        },
        register() {
            uni.navigateTo({
                url: "../register/register"
            })
        },
        outin(){
            uni.clearStorage();
            uni.reLaunch({
                url:"../login/login"
            })
        }
    }
}
